.App {
  text-align: left;
  padding-left: 300px;
  padding-right: 300px;
  margin: 0 auto;
  padding-bottom: 300px;
}

.App p {
  font-size: 14px;
}

.App .greeting {
  text-align: center;
}

.question {
  margin-bottom: 100px;
}

.question.hide {
  display: none;
}

.question .check {
  font-size: 16px;
  padding: 8px;
  cursor: pointer;
}

.options {
  padding-left: 30px;
}

.options p label {
  cursor: pointer;
}

.options .green {
  color: green;
  font-weight: 600;
}

.left-sidebar {
  position: absolute;
  top: 0;
  left: 30px;
  width: 250px;
  bottom: 0;
  overflow: hidden;
  padding-right: 10px;
}

.left-sidebar .select-el {
  max-width: 100%;
}

.right-sidebar {
  position: absolute;
  top: 0;
  right: 30px;
  width: 210px;
  bottom: 0;
}

.sidebar .select-el {
  padding: 10px 10px;
  width: 120px;
  font-size: 14px;
}

.clearfix::after {
  content: "";
  clear: both;
  display: table;
}

.tag-selection {
  margin-bottom: 60px;
}

.select-el {
  display: block;
  font-size: 14px;
  height: 36px;
  line-height: 34px;
  padding: 0 8px;
  margin-bottom: 16px;
}

.cell-q {
  display: block;
  float: left;
  width: 28px;
  height: 28px;
  font-size: 14px;
  line-height: 26px;
  border: 1px solid #000;
  margin: 5px 10px 5px 0px;
  text-align: center;
  cursor: pointer;
}

.cell-q.active {
  color: #fff;
  background-color: grey;
}
